.body {
  background: var(--color4);
  font-family: "Jost Font";
  width: 100vw;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}

.web {
  display: flex;
  flex-direction: column;
  width: 100%;
}

/*Main*/

.background-main {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  min-height: 100vh;
}

.background-main::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../images/home.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  filter: brightness(0.3); /* Aplica el filtro de brillo */
  z-index: 0; /* Asegúrate de que el pseudo-elemento esté detrás del contenido */
}

.main {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: inherit;
  height: 100vh;
}

.main img {
  filter: drop-shadow(2px 4px 6px var(--color2));
}

/*Texto Main*/

.text-home {
  display: flex;
  flex-direction: column;
  width: 1000px;
  gap: 30px;
  padding-top: 2rem;
  align-items: center;
}

.titulo-home {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.sub-text-home {
  display: flex;
  justify-content: space-evenly;
  gap: 10%;
  width: 100%;
  font-size: clamp(0.3em, 0.4em + 1vw, 0.8em);
}

.sub-text-home a {
  color: var(--color4);
  text-decoration: none;
  background-color: transparent;
  padding: 6px 10px 6px 10px;
  border: 3px solid var(--color1);
  border-radius: 15px;
  transition: 0.5s;
}

.sub-text-home a:hover {
  background-color: var(--color1);
  color: var(--color4);
}

/*Titulos Main*/

.main h1 {
  text-transform: uppercase;
  font-size: 60px;
  margin: 0;
  color: var(--color4);
  font-size: clamp(0.7em, 1em + 1vw, 2em);
}

.main h2 {
  text-transform: uppercase;
  margin: 0;
  color: var(--color4);
  font-size: 40px;
  font-size: clamp(0.6em, 1em + 1vw, 1.8em);
}

.main h3::first-letter {
  text-transform: capitalize;
}

.main h3 {
  color: var(--color4);
  font-size: clamp(0.5em, 0.8em + 1vw, 1.2em);
}
.main p {
  text-transform: uppercase;
  margin: 0;
  color: var(--color4);
  font-size: 15px;
  font-size: clamp(0.3em, 0.6em + 1vw, 1em);
}
/*voluntariado*/

.voluntariado {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: var(--color4);
  width: 100%;
  flex-direction: column;
  height: auto;
  padding: 2rem 4rem;
  min-height: 100vh;
}

.tipo-vo-texto {
  gap: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.tipo-vo-texto h3 {
  color: var(--color1);
}
.tipo-vo-texto p {
  word-wrap: break-word;
  font-size: 25px;
  font-size: clamp(0.3em, 0.6em + 1vw, 1em);
}
.tipo-vo-texto h1::first-letter,
.tipo-vo-texto p::first-letter {
  text-transform: capitalize;
}

/*Tipo de Voluntariado*/

.card-container {
  display: grid;
  width: 85%;
  height: auto;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem;
  align-items: center;
  justify-content: center;
}

.card-item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.card-img {
  width: 100%;
  height: 50%;
}
.card-img img {
  border-radius: 15px;
  border: 1px var(--color2) solid;
  width: 100%;
  height: 100%;
}
.card-text h2 {
  margin-bottom: 0.5rem;
}
.card-text {
  height: 50%;
  width: 100%;
  text-align: center;
  word-break: break-all;
  padding-top: 1rem;
}
.card-text a {
  color: var(--color3);
  text-decoration: none;
  border-bottom: 3px solid var(--color1);
}
/*Proceso de Voluntariado*/
#registro-processo {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  background: url(../images/fundo.png);
  background-size: cover;
  background-position: center;
  background-attachment: local;
  z-index: 0;
  
}
.proceso-voluntariado {
  display: flex;
  flex-direction: column;
  gap: 2.8rem;
  align-items: flex-start;
  width: 100%;
  background-color: rgba(149, 135, 32, 0.3);
  padding: 3rem;
}

.proceso-voluntariado h1 {
  padding: 10px 0;
  color: var(--color1);
  font-size: clamp(0.8em, 1em + 1vw, 1.5em);
}
.proceso-voluntariado h1::first-letter {
  text-transform: capitalize;
}
h2.h2-processo-voluntariado {
  margin-bottom: 0;
}
.proceso-voluntariado h2,
.proceso-texto.contato h2 {
  color: var(--color1);
  font-size: clamp(0.5em, 0.8em + 1vw, 1em);
}
.proceso-texto.contato .h2-processo-voluntariado {
color: var(--color3);
}
.proceso-texto.contato p {
  color: var(--color4);
}
.proceso-voluntariado p a {
  text-decoration: none;
  color: var(--color4);
  border-bottom: 2px var(--color1) solid;
  transition: all 0.5s;
}

.proceso-voluntariado p a:hover {
  filter: brightness(2);
}

.proceso-texto {
  display: flex;
  flex-direction: column;
  gap: 0;
  justify-content: center;
  align-items: self-start;
  color: var(--color3);
  text-align: left;
}

.proceso-texto p {
  margin: 0;
  word-wrap: break-word;
}

.proceso-texto h2::first-letter,
.proceso-texto.contato h2::first-letter,
.proceso-texto p::first-letter {
  text-transform: capitalize;
}

/* processo de liderança */
.proceso-voluntariado.lideranca h1 {
  padding: 10px 0;
  color: var(--color2);
  font-size: clamp(0.8em, 1em + 1vw, 1.5em);
}
.proceso-texto-lideranca h2 {
  color: var(--color2);
  font-size: clamp(0.5em, 0.8em + 1vw, 1em);
}
.proceso-voluntariado.lideranca h1::first-letter {
  text-transform: capitalize;
}

.proceso-voluntariado-lideranca p a {
  text-decoration: none;
  color: var(--color4);
  border-bottom: 2px var(--color2) solid;
  transition: all 0.5s;
  font-size: clamp(0.3em, 0.4em + 1vw, 0.8em);
}
.proceso-texto-lideranca {
  display: flex;
  flex-direction: column;
  align-items: self-start;
  color: var(--color4);
  text-align: left;
}
.proceso-voluntariado.lideranca p a:hover {
  filter: brightness(2);
}
.proceso-texto-lideranca p {
  margin: 0;
  word-wrap: break-word;
}

.proceso-texto-lideranca h2::first-letter,
.proceso-texto-lideranca p::first-letter {
  text-transform: capitalize;
}

@media only screen and (max-width: 940px) {
  .background-main {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    min-height: 80vh;
    align-items: center;
    justify-content: center;
  }
  .background-main::before {
    width: 100%;
    height: 100%;
    background-image: url("../images/home.jpg");
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    filter: brightness(0.3);
    max-height: 80vh;
    z-index: 0; /* Asegúrate de que el pseudo-elemento esté detrás del contenido */
  }
  .main {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: inherit;
    height: 100%;
  }
  .voluntariado {
    display: flex;
    align-items: center;
    background-color: var(--color4);
    width: 100%;
    flex-direction: column;
    gap: 2rem;
    height: auto;
    min-height: 60%;
    padding: 2rem;
    justify-content: center;
  }
  .tipo-vo-texto {
    height: 20%;
    width: 90%;
  }
  .card-container {
    width: 90%;
    height: auto;
    max-height: 60%;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
  }
  .card-item {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .card-text h2 {
    font-size: clamp(0.3em, 0.4em + 1vw, 1em);
  }
  .card-text p {
    
    font-size: clamp(0.3em, 0.4em + 1vw, 0.8em);
  }
  .card-text a {
    color: var(--color3);
    text-decoration: none;
    border-bottom: 3px solid var(--color1);
    font-size: clamp(0.3em, 0.4em + 1vw, 0.8em);
  }
  .sub-text-home {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    gap: 2rem;
    font-size: clamp(0.3em, 0.4em + 1vw, 0.8em);
    width: 40%;
  }
  #registro-processo {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    background: url(../images/fundo.png);
    background-size: cover;
    background-position: center;
    background-attachment: local;
    z-index: 0;
   
  }
  .proceso-voluntariado {
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
    width: 100%;
    padding: 0;
    margin: 0;
    padding: 2rem;
  }
  .proceso-texto.contato h2 {
    color: var(--color2);
   
  }
  h2.h2-proceso-voluntariado {
    margin-bottom: 0;
  }
}
@media only screen and (max-width: 670px) {
  .body {
    gap: 5rem;
    height: auto;
  }
  .background-main {
    width: 100%;
    height: 100%;
    min-height: 100vh;
  }
  .background-main::before {
    width: 100%;
    height: 100%;
    max-height: 100vh;
  }
  .main {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: inherit;
    height: 100vh;
  }
  .voluntariado {
    gap: 1rem;
    height: auto;
    max-height:min-content;
    min-height: 100vh;
    margin-bottom: 2rem;
}
  .card-container {
    display: grid;
    width: 85%;
    height: auto;
    grid-template-columns: 1fr;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    max-height: max-content;
  }
  .card-item {
    width: 100%;
    height: 400px;
    display: flex;
    flex-direction: column;
    max-height: 300px;
  }
  .card-img {
    width: 100%;
    height: 60%;
    object-fit: cover;
  }
  .card-img img {
    border-radius: 15px;
    border: 1px var(--color2) solid;
    width: 100%;
    }
    .card-text {
      height: 40%;
      width: 100%;
      text-align: center;
      word-break: break-all;
      padding-top: 1rem;
    }
      #registro-processo {
          flex-direction: column;   
      }
  
    .proceso-voluntariado {
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
      align-items: flex-start;
      width: 100%;
      background-color: rgba(159, 152, 98, 0.345);
      padding: 3rem;  
      
  }
  .proceso-texto-lideranca h2{
    color:  var(--color1);
  }
  .proceso-texto p ,.proceso-texto.contato p {
    color:var(--color4)
}
.proceso-voluntariado h1{
  background-image: linear-gradient(to right, var(--color1), var(--color2));
    background-clip: text;
    -webkit-background-clip: text; /* Prefijo para navegadores WebKit */
    color: transparent;
}
.proceso-voluntariado.lideranca h1{
  background-image: linear-gradient(to right, var(--color4),var(--color4) , var(--color2));
  background-clip: text;
  -webkit-background-clip: text; /* Prefijo para navegadores WebKit */
  color: transparent;
}
}
@keyframes voluntariado-imagen {
  0% {
    background: url("../images/voluntariado\ 1.jpeg");
  }
  33% {
    background: url("../images/voluntariado\ 2.jpeg");
  }
  66% {
    background: url("../images/voluntariado\ 3.jpeg");
  }
  100% {
    background: url("../images/voluntariado\ 1.jpeg");
  }
}
